.App {
  text-align: center;
  background-color: #2a2a2a;
}

.grid-container {
  display: grid;
  grid-template-columns: 200px 200px;
  grid-row: auto auto;
  grid-column-gap: 15%;
  grid-row-gap: 10%;
  margin-top:5%;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
